import React, { useEffect, useState } from 'react';
import LayoutPage from '../Layout/layout';
import firebase from '../firebase';
import { NavLink } from 'react-router-dom';

const Student = () => {
    const [todoList, setTodoList] = useState([]);
    const [code, setCode] = useState('');
    const [name, setName] = useState('');
    const [password, setPassword] = useState('');
    const [isEdit, setIsEdit] = useState(false);
    const [id, setId] = useState('');
    useEffect(() => {

        const getProduct = () => {
          const todoRef = firebase.database().ref('student');
          todoRef.on('value', (snapshot) => {
            const todos = snapshot.val();
            const data = [];
            for (let id in todos) {
              data.push({ id, ...todos[id] });
            }
            setTodoList(data);
    
          });
        }
        getProduct();
    
      }, [])
      const edit = (key) => {
          setCode(key.mssv);
          setName(key.hoten);
          setPassword(key.pass);
          setIsEdit(true);
          setId(key.id);
      }
      const add = () =>{
        setCode('');
        setName('');
        setPassword('');
        setIsEdit(false);
      }
      const addStudent = () => {
        const addSt = firebase.database().ref('student')
        let data = {
            mssv: code,
            hoten: name,
            pass: password
        }
        addSt.push(data);
        add();
      }
      const EditStudent = () => {
        const addSt = firebase.database().ref('student/'+id)
        let data = {
            mssv: code,
            hoten: name,
            pass: password
        }
        addSt.update(data);
        add();
        // console.log(data)
      }
      const DeleteStudent = (id) => {
        const addSt = firebase.database().ref('student/'+id)
        addSt.remove()
      }
    return(
        <LayoutPage>
            <h3 className="text-center">Quản lý thí sinh</h3>
            <div className="text-right">
                <button onClick={add} data-toggle="modal" className="btn btn-primary" data-target="#myModal">Thêm thí sinh</button>
            </div>
      <table className="table table-striped">
        <thead>
          <tr>
              <th>MSSV</th>
              <th>Họ tên</th>
            <th>#</th>
          </tr>
        </thead>
        <tbody>
          {todoList ? todoList.map((key, index) => (
            <tr>
              <td>{key.mssv}</td>
              <td>{key.hoten}</td>
              <td>
                  
                <NavLink to={'#'}><button className="btn btn-secondary" data-toggle="modal" data-target="#myModal" onClick={() => edit(key)}>Sửa</button></NavLink>
                <NavLink to={'#'}><button className="btn btn-danger" onClick={()=>DeleteStudent(key.id)}>Xóa</button></NavLink>
              </td>
            </tr>
          )) : null}

        </tbody>
      </table>
      <div id="myModal" className="modal fade" role="dialog">
  <div className="modal-dialog">
    {/* Modal content*/}
    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal">×</button>
        <h4 className="modal-title">{isEdit ? 'Sửa thông tin thí sinh' : 'Thêm thông tin thí sinh'}</h4>
      </div>
      <div className="modal-body">
          <div className="form-group">
              <label>Mã số sinh viên: </label>
              <input className="form-control" defaultValue={code} onChange={(e) => setCode(e.target.value)} />
          </div>
          <div className="form-group">
              <label>Họ tên sinh viên: </label>
              <input className="form-control" defaultValue={name} onChange={(e) => setName(e.target.value)}/>
          </div>
          <div className="form-group">
              <label>Mật khẩu đăng nhập: </label>
              <input className="form-control" defaultValue={password}  onChange={(e) => setPassword(e.target.value)}/>
          </div>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-primary" onClick={isEdit ? EditStudent : addStudent } data-dismiss="modal">{isEdit ? 'Lưu lại' : 'Thêm mới'}</button>
        <button type="button" className="btn btn-default" data-dismiss="modal">Thoát</button>
      </div>
    </div>
  </div>
</div>

        </LayoutPage>
    )
}
export default Student;